<template>
  <div class="container">

    <app-header></app-header>

    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-5">
        <router-view></router-view>
      </div>
    </div>

    <app-navbar></app-navbar>

    <vue-snotify></vue-snotify>
  </div>
</template>

<script>
  import Header from '@/views/partials/Header'
  import Navbar from '@/views/partials/Navbar'

  export default {
    components: {
      AppHeader: Header,
      AppNavbar: Navbar
    }
  }
</script>

<style lang="scss">
  .mb-5, .my-5 {
      margin-bottom: 4rem!important;
  }

  .mt-5, .my-5 {
      margin-top: 4rem!important;
  }

  hr {
    margin-top: 0;
    margin-bottom: 0;
  }

  .card-body {
    padding-top: .5rem;
  }

  .avatar {
    width: 3rem;
    height: 3rem;
  }

  .loading-header {
    margin-top: 7.25rem;
  }

  .loading-footer {
    margin-bottom: 2.25rem;
  }

  .highlight {
    background-color: rgb(209, 236, 177)
  }

</style>



